<template>
    <div class="shopping">
      <div class="card" v-for="(i,index) in myshopping">
        <img :src="JSON.parse(i.images)[0]"/>
        <p>{{i.name}}</p>
        <p>{{ i.price }}</p>
      </div>
    </div>
  </template>
  
  <script>
    export default {
      computed:{
        
      },
      data(){
        return{
          myshopping:[],
        }
      },
      methods:{
        mysp(){
          let byuser=this.$route.params.user
          console.log(byuser);
          this.$api.getmyshopping({byuser:byuser}).then(res=>{
            console.log(res.data.data);
            this.myshopping=res.data.data
            
          })
        }
      },
      mounted(){
        this.mysp()
      }
    }
  </script>
  <style lang="less" scoped>
    img{
    width: 100%;height: 250px;object-fit: cover;border-radius: 10px 10px 0 0;
    }
     .shopping{
      display: flex;
      flex-wrap: wrap;
      padding: 10px;
     }
     .card{
        --n:4;
        // --gap: calc((100% - 200px * var(--n)) / var(--n) /2);
        --gap: calc((100% - 200px * var(--n)) / var(--n) /2);
        width: 200px;
        height: 300px;
        border-radius: 10px;
        background: rgb(255, 255, 255);
        margin: 10px var(--gap);
     }
  </style>
  